import React, { useEffect, useState } from "react";
import { useStoreon } from "storeon/react";
import Spinner from "../../Views/Spinner";
import { Link, useLocation, useNavigate } from "react-router-dom";

import style from "./styles/info.module.scss";
import { changeStateBackButton, handlerWarningInfoMessageResponse, serialaizerInfoMenu, setDataButtonBottonTrigger } from "../../helpers";
import Message from "../../Views/Messages/Message/Message";
import NativeListMenu from "../../Views/NativeListMenu/NativeListMenu";
import { Dimensions, SafeAreaView, StyleSheet, ScrollView } from 'react-native-web';

const Info = () => {
  const { dispatch, tg, schemaColorList } = useStoreon("tg", 'schemaColorList');
  const { state } = useLocation();
  const navigate = useNavigate();
  const [buttonsInfo, setButtonsInfo] = useState([]);
  const [messageInForm, setMessageInForm] = useState({
    textMessage: "",
    colorMessage: "",
  });

  useEffect(() => {

    window.localStorage.setItem('infoArtical', '')
    dispatch("setTextHeader", state?.name_button ?? "Информация");
    dispatch("setActiveHeaderText", true);

    setDataButtonBottonTrigger(false, tg);
    changeStateBackButton(tg);
    tg.BackButton.onClick(() => navigate("/button_my_profile", {
      state: {
        name_button: state?.old_name_button ?? 'подписка'
      }
    }));

  }, []);

  useEffect(() => {

    const params = {
      url: "/api_get_info/",
      page_id: state?.action_page_id ?? 7,
      dataRequst: (res) => {
        const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
        if (isWarning) return;
        setButtonsInfo(
          res?.result?.buttons?.map((el, i) => {
            return {
              text: el.name,
              slug: i + 1,
              context: el.text,
            };
          })
        );
      },
    };
    dispatch("getContextInfo", params);

  }, []);


  const handlerChangeScreen = (url) => {
    navigate(url)
  }
  const styles = StyleSheet.create({
    conteiner: {
      width: '100%',
      position: 'relative',
      flex: 1,
    }
  })
  const margin = Dimensions.get('window').width - 320;
  return (
    <SafeAreaView style={[styles.container, {
      // backgroundColor: schemaColorList.colorFoneBackgroundElement,
      width: '100%',
      marginTop: margin / 4,
    }]}>
      <Message
      noicon
        message={messageInForm.textMessage}
        colorMessage={messageInForm.colorMessage}
      />
      <ScrollView style={[styles.container, {
                paddingLeft: margin / 2,
                paddingRight: margin / 2,
      }]}>
        {buttonsInfo.length ? (
          <ul className={style["info__container-list"]}>
            {buttonsInfo.map((el, i) => {
              console.log({el})

              return (
                <div>
                {
                    i===3?
                      <div className={style["info__items-line"]} ></div>
                      :null
                  }
                <li key={i} className={style["info__items"]}>
                  <Link
                    to={`/button_my_profile/button_info/${el.slug}`}
                    state={{
                      old_name_button: state?.name_button ?? "Информация",
                      context: el.context,
                      action_page_id: state?.action_page_id ?? 7,
                      title: el.text,
                      slug: el.slug,
                    }}
                    onClick={() => {
                      window.localStorage.setItem('infoArtical', el.slug);
                      el.slug
                        ? dispatch("setActiveHeaderText", true)
                        : dispatch("setActiveHeaderText", false)
                    }
                    }
                  >
                    <div className={style["info__items-text"]}>
                      {el.text}
                    </div>
                  </Link>
                </li>
              </div>  
              );
            })
            
            }
          </ul>
        ) : <Spinner />}

{/* <NativeListMenu
  list={serialaizerInfoMenu(buttonsInfo)}
  handlerChangeScreen={handlerChangeScreen}
  colorFoneCardInfo={schemaColorList.colorFoneCardInfo}
  colorText={schemaColorList.colorText}
  bgColor={schemaColorList.colorFoneBackground}
  toggleBgColor={schemaColorList.toggleBgColor}
  colorFoneBackground={schemaColorList.colorFoneBackground}
  colorFoneBackgroundElement={schemaColorList.colorFoneBackgroundElement}
/> */}
      </ScrollView>
    </SafeAreaView>
  );
};

export default Info;
