import React, { useEffect, useState } from "react";
import { Outlet, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";
import ModalWindow from "../../Views/ModalWindow/ModalWindow";
import Header from "../Header/Header";
import { exclusion, getCookie, setCookie } from "../../helpers";
import { Modal, Alert, View, Pressable, Text, StyleSheet } from 'react-native-web';
import Icon from "../../Views/Icon/Icon";
import { errorCanceled, goodwork } from "../../images";

const Layout = ({

}) => {

    const { activeBackMenu, modal, dispatch, tg, schemaColorList } = useStoreon('activeBackMenu', 'modal', 'tg', 'schemaColorList');
    const navigate = useNavigate();

    const closeModal = () => {
        dispatch('closeModal')
    }

    useEffect(() => {
        if (window.localStorage.getItem('lastUrl')) {
            navigate(window.localStorage.getItem('lastUrl'));
        }
        if (window.localStorage.getItem('itemMenu')) {
            dispatch('setItemMenu', window.localStorage.getItem('itemMenu'))
        }
    }, [])

    useEffect(() => {
        // closeModal();
        document.querySelector('.goto').scrollIntoView({ block: 'start', behavior: 'smooth' });
        const pathname = window.location.pathname
        if (exclusion(pathname))
            window.localStorage.setItem('lastUrl', pathname)
    }, [window.location.href.split('/').pop()])

    useEffect(() => {

        tg.onEvent('viewportChanged', e => {
            if (!e.isStateStable) {
                const smoothScrollUp = (step) => {
                    // const currentPositionModal = document.querySelector('.modalwindow__content').scrollTop;
                    // document.querySelector('.modalwindow__content').scroll(0, currentPositionModal - step);

                    const currentPosition = document.querySelector('.main-context').scrollTop;
                    document.querySelector('.main-context').scroll(0, currentPosition - step);
                    return () => tg.offEvent('viewportChanged')
                }
                smoothScrollUp(10)
            }
        });

    }, [tg])

    const styles = StyleSheet.create({
        centeredView: {
            flex: 1,
            justifyContent: 'center',
            alignItems: 'center',
            //   marginTop: 22,
            backgroundColor: `rgba(0,0,0,.9)`,
            //   height: '100vh',
        },
        modalView: {
            minWidth: 300,
            minHeight: 216,
            backgroundColor: schemaColorList.colorFoneBackgroundElement,
            //   boxShadow: `0 4 14 ${schemaColorList.boxShadow}`,
            borderRadius: 14,
            margin: 20,
            //   backgroundColor: 'white',
            borderRadius: 20,
            padding: 35,
            alignItems: 'center',
            shadowColor: '#000',
            shadowOffset: {
                width: 0,
                height: 2,
            },
            shadowOpacity: 0.25,
            shadowRadius: 4,
            elevation: 5,
        },
        button: {
            padding: 17,
            elevation: 2,
            minWidth: `100%`,
            minHeight: 48,
            border: `1px solid ${schemaColorList.borderColor}`,
            borderRadius: 8,
        },
        buttonCloseCancel: {
            border: `1px solid var(--red-color)`,
        },
        buttonOpen: {
            //   backgroundColor: '#F194FF',
        },
        buttonClose: {
            //   backgroundColor: '#2196F3',
        },
        textStyle: {
            paddingTop: 5,
            color: schemaColorList.colorText,
            fontWeight: 'bold',
            textAlign: 'center',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        textStyleCancel: {
            paddingTop: 5,
            color: schemaColorList.colorText,
            fontWeight: 'bold',
            textAlign: 'center',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },

        modalText: {
            color: `var(--red-color)`,
            marginBottom: 15,
            textAlign: 'center',
            fontFamily: 'Inter',
            fontWeight: 600,
            fontSize: 17,
            lineHeight: 22,
            display: 'flex',
            alignItems: 'center',
            textAlign: 'center',
            letterSpacing: -0.408,
        },
        containerBtn: {
            flex: 1,
            flexDirection: 'row',
            gap: 10,
            alignSelf: 'baseline',
        }
    });

    return (
        <React.Fragment>
            {/* <ModalWindow
                show={modal.show}
                content={modal.content}
                addClass={modal.addClass}
                closeModal={closeModal}
            /> */}
            <Modal
                // content = 'text'
                // show = boolean
                // contentBtn = 'надпись кнопки закрытия'
                // error = boolean image
                animationType="slide"
                transparent={true}
                visible={modal.show}
                onRequestClose={() => {
                    closeModal()
                    modal.path && navigate(modal.path, {
                        ...modal.state
                    })
                }}>
                <View style={styles.centeredView}>
                    <View style={styles.modalView}>
                        <View><Icon
                            image={modal?.error ? errorCanceled : goodwork}
                            width={35}
                            height={35}
                            mb={15}

                        /></View>
                        <Text style={styles.modalText}>{modal.content ?? ' ** text ** '}</Text>
                        <View style={[styles.containerBtn, {
                            width: modal?.contentCancelBtn ? 110 : '100%'
                        }]}>
                            <Pressable
                                style={[styles.button, styles.buttonClose]}
                                onPress={() => {
                                    modal?.path && navigate(modal.path, {
                                        ...modal.state
                                    })
                                    modal?.actionClickOk && modal.actionClickOk(true)
                                    closeModal()
                                }}>
                                <Text style={styles.textStyle}>{modal.contentBtn ?? 'Ок'}</Text>
                            </Pressable>
                            {
                                modal?.contentCancelBtn ?
                                    <Pressable
                                        style={[styles.button, styles.buttonCloseCancel]}
                                        onPress={() => {
                                            closeModal()
                                        }}>
                                        <Text style={styles.textStyleCancel}>{modal?.contentCancelBtn ?? 'Отмена'}</Text>
                                    </Pressable>
                                    : null
                            }
                        </View>
                    </View>
                </View>
            </Modal>
            <div className={'container'}>
                <Header />
                <main
                    className={"main-context"}
                    style={{
                        // top: activeBackMenu ? `0px` : `-50px`,
                    }}
                >
                    <span className="goto"></span>
                    <Outlet />
                </main>
            </div>
        </React.Fragment>
    )
}


export default Layout;