import { StyleSheet, Text, Pressable, SafeAreaView, Image, Dimensions, View } from "react-native-web"
import { useStoreon } from "storeon/react";

const ItemMainMenu = ({
    item,
    bgColor,
    colorText,
    listImageMenu,
    colorFoneBackgroundElement,
    handlerChangeScreen,
    position,
}) => {
    const { dispatch, tg } = useStoreon('tg');
    const openURl = (url, text) => {
        const callback = (status = false, link) => {
            if (!!status) {
                return window.open(link);
            }
            return false;
        }
        // if(tg.initDataUnsafe?.query_id)
        //     tg.showConfirm(`Перейти в группу?`, e => callback(e, url));
        dispatch('openModal', {
            show: true,
            content: 'Перейти в группу?',
            contentBtn: 'ok',
            actionClickOk: (e) => callback(e, url),
            contentCancelBtn: 'Отмена',
            error: false,
        })
    }
    return (
        <SafeAreaView style={[styles.container, { backgroundColor: colorFoneBackgroundElement, marginRight: 10 }]}>
            <Pressable
                style={styles.menuBottom}
                onPress={() => {
                    if (item.id === 6 || item.id === 30) {
                        return openURl(item.url)
                    }

                    handlerChangeScreen(item.slug)
                }}
            >
                <View
                    style={styles.foneImage}
                >
                </View>

                <Image
                    style={{
                        width: 26,
                        height: 26,
                    }}
                    resizeMode="cover"
                    source={{
                        uri: listImageMenu ? listImageMenu.filter((el, i) => i === position)[0] : item.image
                    }}
                />
                <Text
                    style={[styles.menuTextBottom, { color: colorText }]}
                >{item.name}</Text>
            </Pressable>
        </SafeAreaView>
    )
}


export default ItemMainMenu;

const width = Dimensions.get('window').width - 40;
const styles = StyleSheet.create({
    foneImage: {
        backgroundColor: 'rgba(228, 58, 62, 0.2)',
        width: 44,
        height: 44,
        position: 'absolute',
        borderRadius: 7,
        top: 15,
    },
    container: {
        width: width / 2 - 10,
        height: 128,
        maxWidth: 152,
        borderRadius: 14,

    },
    menuBottom: {
        width: '100%',
        paddingHorizontal: 10,
        justifyContent: 'center',
        alignItems: 'center',
        flex: 1,
    },
    menuTextBottom: {
        width: '100%',
        textAlign: 'center',
        marginTop: 20,
        fontSize: 14,
        minHeight: 35,
        fontWeight: 600,
        lineHeight: 20,
    }
})