import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useStoreon } from "storeon/react";

import style from './styles/detaliInfo.module.scss'
import { changeStateBackButton, handlerWarningInfoMessageResponse, setDataButtonBottonTrigger } from "../../../../helpers";
import Message from "../../../../Views/Messages/Message/Message";
import Spinner from "../../../../Views/Spinner";
import { Dimensions, SafeAreaView, ScrollView, StyleSheet } from "react-native-web";
import Icon from "../../../../Views/Icon/Icon";
import { frame, play } from "../../../../images";


const InfoContext = () => {
    const { dispatch, tg, schemaColorList } = useStoreon('tg', 'schemaColorList');
    const { state } = useLocation();
    const navigate = useNavigate();
    const context = state?.context;
    const [newText, setNewText] = useState('');
    const [link, setLink] = useState([]);
    const [messageInForm, setMessageInForm] = useState({
        textMessage: "",
        colorMessage: "",
    });

    useEffect(() => {

        dispatch("setTextHeader", state?.name_button ?? "Информация");
        dispatch("setActiveHeaderText", true);

        setDataButtonBottonTrigger(false, tg);
        changeStateBackButton(tg);
        tg.BackButton.onClick(() => navigate('/button_my_profile/button_info', {
            state: {
                name_button: state?.old_name_button,
                slug: state?.slug,
                action_page_id: state?.action_page_id,
            }
        }));

    }, [])

    useEffect(() => {

        const params = {
            url: "/api_get_info/",
            page_id: state?.action_page_id ?? 7,
            dataRequst: (res) => {
                const isWarning = handlerWarningInfoMessageResponse(res, dispatch);
                if (isWarning) return;
                const arrInfo = res?.result?.buttons?.map((el, i) => {
                    return {
                        text: el.name,
                        slug: i + 1,
                        context: el.text,
                    };
                });
                if (window.localStorage.getItem('infoArtical')) {
                    if (arrInfo === undefined) return;
                    const context = arrInfo.filter(item => item.slug === +window.localStorage.getItem('infoArtical'))
                    setNewText(context[0].context.split(' ').map((el, i) => el.includes('https://') ? null : el)?.filter(el => el !== null).join('  '))
                    setLink(context[0].context.split(' ').map((el, i) => el.includes('https://') ? el : null)?.filter(el => el !== null))
                    // setNewText(context[0].context.split(' ').map((el, i) => el.includes('https://') ? `<a href=${el} onClick="window.Telegram.WebApp.close()">${el}</a>` : el).join('  '))
                }
            },
        };
        if (!context) {
            dispatch("getContextInfo", params);
        }
    }, []);

    useEffect(() => {
        if (context === undefined) return;
        if (context)
            setNewText(context.split(' ').map((el, i) => el.includes('https://') ? null : el)?.filter(el => el !== null)?.join('  '));
        setLink(context.split(' ').map((el, i) => el.includes('https://') ? el : null)?.filter(el => el !== null))
        // setNewText(context.split(' ').map((el, i) => el.includes('https://') ? `<a href=${el} onClick="window.Telegram.WebApp.close()">${el}</a>` : el).join('  '));
    }, [context])

    const styles = StyleSheet.create({
        conteiner: {
            width: '100%',
            position: 'relative',
            flex: 1,
        },
        scroll: {
            alignItems: 'center',
        }
    })
    const margin = Dimensions.get('window').width - 320;
    const callback = (status, link) => {
        if (status) {
            return window.open(link);
        }
        return false;
    }
    console.log({link})
    return (
        <ScrollView
            style={[styles.container, {
                // backgroundColor: schemaColorList.colorFoneBackgroundElement,
                width: '100%',
                marginTop: margin / 4,
                paddingLeft: margin / 2,
                paddingRight: margin / 2,
            }]}
        >
            <Message
                noicon
                message={messageInForm.textMessage}
                colorMessage={messageInForm.colorMessage}
            />
            {
                newText ?
                    <div
                        className={style['detali-info__container-block-info']}
                    >
                        <div
                            className={style['detali-info__container']}
                            dangerouslySetInnerHTML={{ __html: newText }}>
                        </div>
                        {
                            link.length &&
                            <div
                                className={style['detali-info__container-btn']}
                            >
                                <div
                                    className={style['detali-info__text-youtube']}
                                >Смотреть ролик на Youtube</div>
                                <Icon
                                    image={frame}
                                    width={202}
                                    height={115}
                                    className={style['detali-info__play-container-image']}
                                >

                                </Icon>

                                <Icon
                                    image={play}
                                    width={20}
                                    height={20}
                                    className={style['detali-info__play-image']}
                                    onClick={() => {
                                        dispatch('openModal', {
                                            show: true,
                                            content: 'Открыть youtube?',
                                            contentBtn: 'ok',
                                            actionClickOk: (e) => callback(e, link[0]),
                                            contentCancelBtn: 'Отмена',
                                            error: false,
                                        })
                                    }}
                                />
                            </div>
                        }

                    </div>
                    : <Spinner />
            }
        </ScrollView>
    )
}

export default InfoContext;