import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { useStoreon } from "storeon/react";
import { useNavigate } from "react-router-dom";
import { Swiper, SwiperSlide } from 'swiper/react';
import { homePng } from "../../images";
import Icon from "../../Views/Icon/Icon";

import 'swiper/css';
import style from './styles/header.module.scss';
import { setDataButtonBotton } from "../../helpers";
import cogoToast from 'cogo-toast';
import{
    homeWhite,
    advanceWhite,
    cartWhite,
    create_requestWhite,
    starWhite,
    subscribeWhite, 
    filtersWhite,

    advanceBlack,
    cartBlack,
    createRequestBlack,
    starBlack,
    subscribeBlack,
    filtersBlack,
    homeBlack,

    advanceRed,
    cartRed,
    createRequestRed,
    starRed,
    subscribeRed,
    filtersRed,
    homeRed,
} from '../../images/_icons/bottomMenu/index';
import { ScrollView } from "react-native-web";

const Header = (

) => {
    const { activeBackMenu, textHeader, tg, selectItemMenu, messagePopup } = useStoreon('activeBackMenu', 'textHeader', 'tg', 'selectItemMenu', 'messagePopup');
    const navigate = useNavigate();
    const { menu, dispatch, schemaColor } = useStoreon('menu', 'schemaColor');

    const [colorScheme, setColorScheme] = useState(window.localStorage.getItem('colorScheme'))
    const [buttons, setButtons] = useState([]);
    const [listImageMenu, setListImageMenu] = useState([]);
    
    const listImageMenuWhite = [
        homeWhite,
        advanceWhite,
        cartWhite,
        create_requestWhite,
        starWhite,
        subscribeWhite, 
        filtersWhite,
    ]
    const listImageMenuBlack = [
        homeBlack,
        advanceBlack,
        cartBlack,
        createRequestBlack,
        starBlack,
        subscribeBlack,
        filtersBlack,
    ]
    const listImageMenuRed = [
        homeRed,
        advanceRed,
        cartRed,
        createRequestRed,
        starRed,
        subscribeRed,
        filtersRed,
    ]

    useEffect(()=>{

    },[])
    
    useEffect(() => {
        setColorScheme(window.localStorage.getItem('colorScheme'));
        console.log('work', schemaColor)
        schemaColor === 'light' ? 
            setListImageMenu(listImageMenuBlack)
            : setListImageMenu(listImageMenuWhite)
    }, [schemaColor]);

    useEffect(() => {

        if (!!messagePopup) {
            const { hide } = cogoToast.warn(
                <div
                    className={style['detali-info__container']}
                    dangerouslySetInnerHTML={{ __html: messagePopup.split(' ').map((el, i) => el.includes('https://') ? `<strong><a style="color: var(--red-color)" href=${el}>${el}</a></strong>` : el).join(' ') }}
                >
                </div>
                , {
                    position: 'top-center',
                    heading: `Уведомление `,
                    style: `marginTop: 15px; width: 90%`,
                    hideAfter: 10,
                    onClick: (e) => {
                        // messagePopup.match(/https:\/\/[^\s\Z]+/i)?.length ? window.open(messagePopup.match(/https:\/\/[^\s]+/i)[0]) : console.log('fack url');
                        dispatch('setMessagePopup', '')
                        return hide()
                    }
                }
                )
            }
    }, [messagePopup])
    useEffect(() => {
        console.log('action ------ ')
        if (window.localStorage.getItem('colorScheme') === 'dark') {
          // если при востановлении в памяти черная но проверяем телеграмм
          document.documentElement.style.setProperty('--text-color', '#ffffff' ); // цвет текста 
          document.documentElement.style.setProperty('--bgc-element-fone', '#222327'); // цвет фона каждого элемента +
          document.documentElement.style.setProperty('--color-fone-background', '#131116');
          document.documentElement.style.setProperty('--placeholder', 'rgba(255,255,255,.6)');
          document.documentElement.style.setProperty('--box-shadow', 'rgba(0,0,0,.05)');
          document.documentElement.style.setProperty('--text-color-link', '#6987D4');
          document.documentElement.style.setProperty('--border-color', 'rgba(255, 255, 255, 0.2)');
          document.documentElement.style.setProperty("--bg-native-select-color",'#222327');
          
          dispatch('setSchemaColor', 'dark');
          dispatch('setSchemaColorsList', {
            colorText : '#ffffff',
            colorFoneBackgroundElement : '#222327',  
            colorFoneBackground : '#131116',  
            placeholder: 'rgba(255,255,255,.6)',
            boxShadow: 'rgba(0,0,0,.05)',
            textColorLink : '#6987D4',  
            borderColor: 'rgba(255, 255, 255, 0.2)',
            invertTheme: 0,
          });
          // setColorTheme((c) => 'dark');
          return 
        }
        document.documentElement.style.setProperty('--text-color','#282828' );
        document.documentElement.style.setProperty('--bgc-element-fone', '#ffffff');
        document.documentElement.style.setProperty('--color-fone-background', '#f8f7f6');
        document.documentElement.style.setProperty('--placeholder', 'rgba(40,40,40,.6)');
        document.documentElement.style.setProperty('--box-shadow', 'rgba(0,0,0,.05)');
        document.documentElement.style.setProperty('--text-color-link', '#6987D4');
        document.documentElement.style.setProperty('--border-color', '#E4E4E4');
        document.documentElement.style.setProperty("--bg-native-select-color",'#FFFFFF');
    
        dispatch('setSchemaColor', 'light');
        dispatch('setSchemaColorsList', {
          colorText : '#282828',
          colorFoneBackgroundElement : '#ffffff',
          colorFoneBackground : '#F8F7F6',  
          placeholder: 'rgba(40,40,40,.6)',
          boxShadow: 'rgba(0,0,0,.05)',
          textColorLink : '#6987D4',  
          borderColor: '#E4E4E4',
          invertTheme: 1,
        });
        // setColorTheme((c) => 'light');
        return 
      }, [ ]);

      
    useEffect(() => {

        setDataButtonBotton(false, tg);
        tg.onEvent('viewportChanged', (event) => {
            if (!event.isStateStable) {
                tg.expand()
            }
        });
    }, [])

    useEffect(() => {
        dispatch('setMenu')
    }, [])


    useEffect(() => {
        if (!!!menu?.content) return
        setButtons([            
            {
                action_page_id: null,
                additional_info: "unit",
                id: 999,
                image: `${homePng}`,
                name: "",
                parent_page_id: 1,
                slug: '',
                text: "Главная",
                class: '',
                can_click: {
                    message: "test",
                    redirect_payment: true,
                    status: true,
                },
            },
            ...menu.content.buttons
        ])

    }, [menu?.content?.buttons, schemaColor, textHeader, activeBackMenu])

    const goTo = () => {
        document.querySelector('.goto').scrollIntoView({ block: 'center', behavior: 'smooth' })
    }
    const openURl = (url, text) => {
        const callback = (status = false, link) => {
            debugger
            if (!!status) {
                return window.open(link);
            }
            return false;
        }
        // tg && +tg.version >= 6 ?
        //     tg.showConfirm(`Перейти в группу?`, e => callback(e, url))
        //     : console.log('проблемы с телеграмм')
            dispatch('openModal', {
                show: true,
                content: 'Перейти в группу?',
                contentBtn: 'ok',
                actionClickOk: (e) => callback(e, url),
                contentCancelBtn: 'Отмена',
                error: false,
            })
    }



    return (
        <section>
            <div
                className={classNames({
                    [style['header__menu-wrap--top']]: true,
                })}
                style={{
                    // minHeight: activeBackMenu ? `51px` : `0px`,
                    // height: activeBackMenu ? `51px` : `0px`,
                }}
            >
                <div
                    className={classNames({
                        [style['header__wrap-discription']]: true,
                    })}
                >{!!textHeader && (textHeader[0].toUpperCase() + textHeader?.slice(1).toLowerCase())}</div>
            </div>
            <div
                className={style['header__menu-wrap--bottom']}
                >

                {
                    menu?.content && ('buttons' in menu?.content) ?
                    
                    <div
                        className={style['header__list-container']}
                            // spaceBetween={25}
                            // slidesPerView={buttons?.length}
                            // autoplay
                            // style={{ height: '100%' }}
                        // onSlideChange={() => console.log('slide change')}
                        // onSwiper={(swiper) => console.log(swiper)}
                        >
                            {
                                buttons?.map((el, index) => {
                                    const activeIconImage = listImageMenuRed.filter( (item, i) => el.id === +selectItemMenu && i === index )[0]
                                    console.log({activeIconImage}, {selectItemMenu : +selectItemMenu})
                                    return (
                                        <div
                                            key={el.id}
                                        >
                                            {
                                                el.id === 998 ?
                                                    <Icon
                                                        width={18}
                                                        height={18}
                                                        image={ listImageMenu? 
                                                                activeIconImage?
                                                                    activeIconImage
                                                                    : listImageMenu.filter((el, i) => i === index)[0] 
                                                                        : el.image}
                                                        onClick={goTo}
                                                        className={classNames({
                                                            [style['header__list-items']]: true,
                                                            [style['header__list-items--back']]: colorScheme === 'dark',
                                                            [style['header__select-item']]: +selectItemMenu === el.id,
                                                        })}
                                                    />
                                                    :
                                                    el.can_click?.status ?
                                                        <div
                                                            onClick={() => {

                                                                if (el.id === 30) {
                                                                    dispatch('setItemMenu', null);
                                                                    openURl(el.url, '');
                                                                    return
                                                                }
                                                                if (el.id === 6) {
                                                                    dispatch('setItemMenu', null);
                                                                    window.localStorage.setItem('itemMenu', null)
                                                                    openURl(el.url, '');
                                                                    return
                                                                }

                                                                dispatch('setItemMenu', el.id);
                                                                window.localStorage.setItem('itemMenu', el.id)
                                                                dispatch('context', {})
                                                                el.slug ? dispatch('setActiveHeaderText', true) : dispatch('setActiveHeaderText', false);
                                                                navigate(`/${el.slug}`, {
                                                                    state: {
                                                                        action_page_id: el.action_page_id ?? 1,
                                                                        url: window.location.href.split('/').pop(),
                                                                        type: el.additional_info,
                                                                        name_button: el.text,
                                                                        url_redirect: el.url
                                                                    }
                                                                })
                                                            }}
                                                        >
                                                            <Icon
                                                                width={18}
                                                                height={18}
                                                                image={ listImageMenu? 
                                                                    activeIconImage?
                                                                        activeIconImage
                                                                        : listImageMenu.filter((el, i) => i === index)[0] : el.image}
                                                                className={classNames({
                                                                    [style['header__list-items']]: true,
                                                                    [style['header__list-items--back']]: colorScheme === 'dark',
                                                                    [style['header__select-item']]: +selectItemMenu === el.id,
                                                                })}
                                                            />
                                                        </div>
                                                        : <div
                                                            onClick={() => {
                                                                dispatch('setMessagePopup', el.can_click?.message ? el.can_click?.message : '');
                                                                // dispatch('setActiveHeaderText', false)
                                                                dispatch('setItemMenu', 31);
                                                                window.localStorage.setItem('itemMenu', 31)
                                                                if (el.can_click.redirect_payment) {
                                                                    return navigate('/button_my_profile', {
                                                                        state: {
                                                                            name_button: 'мой профиль'
                                                                        }
                                                                    });
                                                                }
                                                                return true;
                                                            }}
                                                        >
                                                            <Icon
                                                                width={18}
                                                                height={18}
                                                                image={ listImageMenu? 
                                                                            activeIconImage?
                                                                                activeIconImage
                                                                                : listImageMenu.filter((el, i) => i === index)[0] : el.image}
                                                                className={classNames({
                                                                    [style['header__list-items']]: true,
                                                                    [style['header__list-items--back']]: colorScheme === 'dark',
                                                                    [style['header__select-item']]: +selectItemMenu === el.id,
                                                                })}
                                                            />
                                                        </div>
                                            }
                                        </div>
                                    )
                                })
                            }
                        </div>

                        : null
                }


            </div>
        </section>
    )
}

export default Header;

